const routes = [
    {
        path: '/',
        redirect: "/WXPay"
    }, {
        path: '/WXPay',
        name: 'WXPay',
        component: () => import('../views/WXPay.vue'),
        meta: {
            isOutSide: true
        }
    }, {
        path: '/WXPayFinish',
        name: 'WXPayFinish',
        component: () => import('../views/WXPayFinish.vue'),
        meta: {
            isOutSide: true
        }
    }, {
        path: '/videoApp',
        name: 'videoApp',
        component: () => import('../views/videoApp.vue'),
        meta: {
            isOutSide: true
        }
    }
]

export default routes
