import Vue from "vue"
//用于详情首页视频简介的
Vue.filter('ellipsis', text => {
    if (text.length > 35) {
        return text.substring(0, 35) + '...';
    } else {
        return text;
    }
})
//将传入的字符串，数字转换为带千位符的两位数小数
Vue.filter('moneyFormat', num => {
    num = num.toString().replace(/\$|\,/g, "");
    var cent = 2;
    var isThousand = 1; //初始化
    // 检查传入数值为数值类型
    if (isNaN(num)) num = "0";

    // 获取符号(正/负数)
    var sign = num == (num = Math.abs(num));

    num = Math.floor(num * Math.pow(10, cent) + 0.50000000001); // 把指定的小数位先转换成整数.多余的小数位四舍五入
    var cents = num % Math.pow(10, cent); // 求出小数位数值
    num = Math.floor(num / Math.pow(10, cent)).toString(); // 求出整数位数值
    cents = cents.toString(); // 把小数位转换成字符串,以便求小数位长度

    // 补足小数位到指定的位数
    while (cents.length < cent) cents = "0" + cents;

    if (isThousand) {
        // 对整数部分进行千分位格式化.
        for (var i = 0; i < Math.floor((num.length - (1 + i)) / 3); i++)
            num =
                num.substring(0, num.length - (4 * i + 3)) +
                "," +
                num.substring(num.length - (4 * i + 3));
    }

    if (cent > 0) return (sign ? "" : "-") + num + "." + cents;
    else return (sign ? "" : "-") + num;
})
//将字符串转成金额1
Vue.filter('moneyFormatType', num => {
    if (num) {
        num = parseFloat(num);
        num = num.toLocaleString();
        let xsd = num.split(".");
        if (xsd.length == 1) {
            return num + ".00";
        }
        if (xsd.length > 1) {
            if (xsd[1].length == 1) {
                return num + "0";
            } else {
                return num;
            }
        }
    } else {
        return '0.00';
    }
});
Vue.filter('moneyCapitalization', money => {
    if (money) {
        let cnNums = new Array("零", "壹", "贰", "叁", "肆", "伍", "陆", "柒", "捌", "玖"); //汉字的数字
        let cnIntRadice = new Array("", "拾", "佰", "仟"); //基本单位
        let cnIntUnits = new Array("", "万", "亿", "兆"); //对应整数部分扩展单位
        let cnDecUnits = new Array("角", "分", "毫", "厘"); //对应小数部分单位
        let cnInteger = "整"; //整数金额时后面跟的字符
        let cnIntLast = "元"; //整型完以后的单位
        let maxNum = 999999999999999.9999; //最大处理的数字
        let IntegerNum; //金额整数部分
        let DecimalNum; //金额小数部分
        let ChineseStr = ""; //输出的中文金额字符串
        let parts; //分离金额后用的数组，预定义
        if (money == "") {
            return "";
        }
        money = parseFloat(money);
        if (money >= maxNum) {
            alert('超出最大处理数字');
            return "";
        }
        if (money == 0) {
            ChineseStr = cnNums[0] + cnIntLast + cnInteger;
            return ChineseStr;
        }
        money = money.toString(); //转换为字符串
        if (money.indexOf(".") == -1) {
            IntegerNum = money;
            DecimalNum = '';
        } else {
            parts = money.split(".");
            IntegerNum = parts[0];
            DecimalNum = parts[1].substr(0, 4);
        }
        if (parseInt(IntegerNum, 10) > 0) { //获取整型部分转换
            let zeroCount = 0;
            let IntLen = IntegerNum.length;
            for (let i = 0; i < IntLen; i++) {
                let n = IntegerNum.substr(i, 1);
                let p = IntLen - i - 1;
                let q = p / 4;
                let m = p % 4;
                if (n == "0") {
                    zeroCount++;
                } else {
                    if (zeroCount > 0) {
                        ChineseStr += cnNums[0];
                    }
                    zeroCount = 0; //归零
                    ChineseStr += cnNums[parseInt(n)] + cnIntRadice[m];
                }
                if (m == 0 && zeroCount < 4) {
                    ChineseStr += cnIntUnits[q];
                }
            }
            ChineseStr += cnIntLast;
            //整型部分处理完毕
        }
        if (DecimalNum != '') { //小数部分
            let decLen = DecimalNum.length;
            for (let i = 0; i < decLen; i++) {
                let n = DecimalNum.substr(i, 1);
                if (n != '0') {
                    ChineseStr += cnNums[Number(n)] + cnDecUnits[i];
                }
            }
        }
        if (ChineseStr == '') {
            ChineseStr += cnNums[0] + cnIntLast + cnInteger;
        } else if (DecimalNum == '') {
            ChineseStr += cnInteger;
        }
        return ChineseStr;
    } else {
        return '';
    }
})
//表格中“用途”一列的长度大于20时则显示省略号2
Vue.filter('purposeFormatType', purpose => {
    if (purpose.length > 20) {
        return purpose.substring(0, 20) + "...";
    } else {
        return purpose;
    }
});
// 将时间戳转换成xxxx-xx-xx xx:xx的形式(后台传的数据是毫秒) 3
Vue.filter('submitTimeFormatType', submitTime => {
    if (submitTime) {
        let time
        if (typeof submitTime == 'number') {
            time = new Date(submitTime * 1000);
        } else {
            time = new Date(submitTime);
        }
        let y = time.getFullYear();
        let m = time.getMonth() + 1;
        m = m < 10 ? "0" + m : m;
        let d = time.getDate();
        d = d < 10 ? "0" + d : d;
        let h = time.getHours();
        h = h < 10 ? "0" + h : h;
        let min = time.getMinutes();
        min = min < 10 ? "0" + min : min;
        return y + "-" + m + "-" + d + " " + h + ":" + min;
    } else {
        return '--'
    }
});
// 将时间戳转换成xxxx-xx-xx xx:xx:xx的形式(后台传的数据是毫秒) 3
Vue.filter('submitTimeFormatFullType', submitTime => {
    if (submitTime) {
        let time = new Date(submitTime);
        let y = time.getFullYear();
        let m = time.getMonth() + 1;
        m = m < 10 ? "0" + m : m;
        let d = time.getDate();
        d = d < 10 ? "0" + d : d;
        let h = time.getHours();
        h = h < 10 ? "0" + h : h;
        let min = time.getMinutes();
        min = min < 10 ? "0" + min : min;
        let s = time.getSeconds();
        s = s < 10 ? "0" + s : s;
        return y + "-" + m + "-" + d + " " + h + ":" + min + ":" + s;
    } else {
        return '--';
    }
});
// 将时间戳转换成xxxx-xx-xx的形式(中国标准时间转成该型式，或者时间戳转成改形式)4
Vue.filter('tableTimeFormatType', submitTime => {
    if (submitTime) {
        let time = new Date(submitTime); //中国标准时间转成该型式，此步是多余的。但是不会报错，所以就写在这了，不想再多写一个方法专门处理，使代码冗余
        let y = time.getFullYear();
        let m = time.getMonth() + 1;
        m = m < 10 ? "0" + m : m;
        let d = time.getDate();
        d = d < 10 ? "0" + d : d;
        return y + "-" + m + "-" + d;
    } else {
        return '--';
    }
});

// 租赁类型的过滤器
Vue.filter('tableLeaseType', leaseType => {
    if (leaseType) {
        if (leaseType == 1) {
            return "场地";
        } else if (leaseType == 2) {
            return "设备/器材";
        } else if (leaseType == 3) {
            return "服装/道具 ";
        } else if (leaseType == 4) {
            return "其他";
        }
    } else {
        return '--';
    }
});

// 收支类型（0=收入；1=支出；2=借款）5
Vue.filter('inoutTypeFormatType', inoutType => {
    if (inoutType == 0) {
        return "收入";
    } else if (inoutType == 1) {
        return "支出";
    } else if (inoutType == 2) {
        return "冲账";
    }
});

// 审批类型（1=借款；2=报销；3=结算）6
Vue.filter('auditTypeFormatType', auditType => {
    if (auditType == 1) {
        return "借款";
    } else if (auditType == 2) {
        return "报销";
    } else if (auditType == 3) {
        return "合同结算";
    } else {
        return "---";
    }
});

// 业务类型（1=借款；2=报销；3=合同结算；4=剧组收入；5=剧组支出）7
Vue.filter('busTypeFormatType', busType => {
    if (busType == 1) {
        return "借款";
    } else if (busType == 2) {
        return "报销";
    } else if (busType == 3) {
        return "合同结算";
    } else if (busType == 4) {
        return "剧组收入";
    } else if (busType == 5) {
        return "剧组支出";
    } else if (busType == 6) {
        return "还款";
    } else if (busType == 7) {
        return "押金退回";
    }
});

// 审批状态（0=待提交；1=待审批；2=已撤回；3=审批中；4=等待财务处理；5=审批拒绝；6=已完成）8
Vue.filter('statusFormatType', status => {
    if (status == 0) {
        return "待提交";
    } else if (status == 1) {
        return "待审批";
    } else if (status == 2) {
        return "已撤回";
    } else if (status == 3) {
        return "审批中";
    } else if (status == 4) {
        return "等待财务处理";
    } else if (status == 5) {
        return "审批拒绝";
    } else if (status == 6) {
        return "已完成";
    } else if (status == 7) {
        return "财务驳回";
    } else {
        return "";
    }
});
// 处理状态（0=发起申请;1=已撤回;2=等待审批;3=已同意;4=已拒绝;5=已完成）9
Vue.filter('dealStatusFormatType', status => {
    if (status == 0) {
        return "发起申请";
    } else if (status == 1) {
        return "已撤回";
    } else if (status == 2) {
        return "等待审批";
    } else if (status == 3) {
        return "已同意";
    } else if (status == 4) {
        return "已拒绝";
    } else if (status == 5) {
        return "已完成";
    }
});

//  过滤函数保留两位小数 10
Vue.filter('twoNumber', num => {
    num = Number(num);
    return num.toFixed(2);
});

//  显示百分比 11
Vue.filter('twoPer', num => {
    num = Number(num);
    return '(' + num.toFixed(2) + '%' + ')';
});
//  显示百分比 12
Vue.filter('twoPerTwo', num => {
    num = Number(num);
    return num.toFixed(2) + '%';
});
//  主页信息(电影、剧集、综艺、动漫、纪录片、其他)
Vue.filter('shootType', status => {
    if (status == 1) {
        return "电影";
    } else if (status == 2) {
        return "剧集";
    } else if (status == 3) {
        return "综艺";
    } else if (status == 4) {
        return "动漫";
    } else if (status == 5) {
        return "纪录片";
    } else if (status == 6) {
        return "其他";
    }
});

