//引入Vue
import Vue from 'vue'
// 引入vuex
import Vuex from 'vuex'
// 引入vuex-persistedstate，用来固化vuex
import createPersistedState from "vuex-persistedstate";

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        appUserInfo: {}
    },
    mutations: {
        setAppUserInfo(state, v) {
            state.appUserInfo = v
        }
    },
    actions: {},
    getters: {},
    plugins: [createPersistedState()],
})